.header-title {
    display: flex;
    align-items: center;
}
.header-title img {
    margin-right: 10px;
}
.containerFrame {
    height: 92vh;  
}
@font-face {
    font-family: 'Gotham Pro';
    src: url(/vk-apps-constructor/static/media/GothaProMed.dd3d9ca5.otf);
    font-weight: 500;
    font-style: medium;
}

@font-face {
    font-family: 'Gotham Pro';
    src: url(/vk-apps-constructor/static/media/GothaProReg.aafeb237.otf);
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'Gotham Pro';
    src: url(/vk-apps-constructor/static/media/GothaProBol.3f9c5a2b.otf);
    font-weight: bold;
    font-style: normal;
}

body {
    --header_background: #3D3D3D;
    --header_alternate_background: #3D3D3D;
    --background_page: #3D3D3D;
    /* --background_content: #3D3D3D; */
    --font-android: Gotham Pro;
    --font-ios: Gotham Pro;
    overflow: hidden!important;
    position: fixed!important;
    width: 100vw;
    background: #3D3D3D;
}

/* Уберает линию после шапки на iOS */
.PanelHeader-bg::after {
    height: 0!important;
}
